import {mapState, mapActions} from 'vuex'
import helper from '@/mixins/helper'
import tool from '@/utils/tool'
import Scroll from "@/components/Scroll"

export default {
    name: "WithdrawList",
    mixins: [helper],
    components: { Scroll },
    data() {
        return {
            testList: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {},],
        }
    },
    computed: {
        ...mapState({
            list: state => state.withdraw.list,
            nomore: state => state.withdraw.nomore,
            loaded: state => state.withdraw.loaded,
        })
    },
    created() {
        // this.getList({})
    },
    methods: {
        ...mapActions({
            getList: 'withdraw/getList',
            loadMore: 'withdraw/loadMore',
        }),
        pullingDown() {
            setTimeout(() => {
                this.testList = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {},]
            }, 3000)
        },
        pullingUp() {
            setTimeout(() => {
                let data = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {},]
                if (this.testList.length > 19) data = []
                this.testList = this.testList.concat(data)
            }, 3000)
        },
    }
}



